<script>
export let nextInstruction
async function timer(time){
        return await new Promise(r => setTimeout(r, time));
    }
async function requestFullScreen(){
    var elem=document.documentElement
    var requestMethod = elem.requestFullScreen || elem.webkitRequestFullScreen || elem.mozRequestFullScreen || elem.msRequestFullScreen;
    if (requestMethod) {
        requestMethod.call(elem)
    }
    await timer(100)
    nextInstruction(1)
    }
</script>
<style>
    .pretty_button{
        background-color:rgb(196, 66, 66);
        width: 200px;
        height: 100px;
        font-size: x-large;
        color: black;
        border-radius: 20px;
        left: calc(50% + -100px);
        top: calc(50% + -50px);
        position: relative;
        border: solid black 5px
    }
    .pretty_button:hover{
        cursor: pointer;
        background-color:rgb(243, 136, 136)
    }
</style>
<h1 style="width:100%; height:200px; text-align: center">This Experiment is Best Viewed In Fullscreen</h1>
<button class="pretty_button" on:click={requestFullScreen}>Go Fullscreen</button>