<script>
    export let breakTruth={truth:false}
    export let nextInstruction=null
    export let previousInstruction=null
    export let forwardKey=" "
    export let backKey="b"
    export let backSkip =1
    export let forwardSkip =1
    export let display = true
    function handleKeydown(e){
        if (breakTruth.truth){
            return
        }
        if (e.key === forwardKey){
            nextInstruction(forwardSkip)
        }
        if(e.key === backKey){
            previousInstruction(backSkip)
        }
    }
</script>
<svelte:window on:keydown={handleKeydown}/>
{#if !breakTruth.truth && display}
    {#if nextInstruction && previousInstruction}
        <h1 style="position:absolute; left:calc(25vw + -200px);top:calc(100vh + -100px); width:400px; height: 50px; text-align: center; border: solid black 2px">Back: B</h1>
        <h1 style="position: absolute; left:calc(75vw + -200px); top:calc(100vh + -100px); width:400px; height: 50px; text-align: center; border: solid black 2px">Next: Space</h1>
    {:else if nextInstruction}
        <h1 style="position: absolute; left:calc(50vw + -200px); top:calc(100vh + -100px); width:400px;  height: 50px; text-align: center; border: solid black 2px">Next: Space</h1>
    {:else}
        <h1 style="position:absolute; left:calc(50vw + -200px);top:calc(100vh + -100px); width:400px; height: 50px; text-align: center; border: solid black 2px">Back: B</h1>
    {/if}
{/if}