<script>
    import Game from "./Game.svelte"
    import Instructions from "./Instructions/Instructions.svelte"
    import Redirect from './Redirect.svelte'
    let instructionsDone=false
    let gameEnd=false
    let gameData=""
    function toGame(){
        instructionsDone = true
    }
    function toDebrief(){
        gameEnd = true
    }
    function getData(data){
        gameData=data
        console.log(data)
    }
</script>
{#if instructionsDone===false}
    <Instructions toGame={toGame} getData={getData}/>
{/if}
{#if (instructionsDone && !gameEnd)}
    <Game toDebrief={toDebrief} gameString={gameData}/>
{/if}
{#if gameEnd}
    <Redirect/>
{/if}

