<svelte:window on:keydown={handleKeydown}/>
<script>
    import RedGreen from "./RedGreen.svelte"
    export let counter=0
    //export let top="50px"
    //export let left ="200px"
    export const viewNumber=2
    let trialSd = 3
    let numTrials =100
    let trial = 1
    let trialData=[]
    let viewPortScale=100
    let blockSize="430px"
    export let gameString=""
    let range=[...Array(viewNumber).keys()]
    let trialStartTime=Date.now()
    export let viewExplore=false
    export let exploitMu=random_int()
    export let exploreMu=random_int()
    export let exploreSelect =false
    export let exploitSelect =false
    export let replaceExploit={truth:false}
    export let keyView=true
    export let clearBoard=false
    export let currentUnderstanding=exploitMu
    export let toDebrief
    let bothInvisible=true
    console.log(gameString)
    //$: oldExploit =replaceExploit
    function migrateLeftExplore(node,{delay=0,duration=500}){
        if (bothInvisible){
            return {
                 delay:0,
                duration:0}
        }
        console.log(`migrateLeftExplore:${true}`)
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${100*u}vw)) `
            }
}
function migrateLeftExploit(node,{replaceExploit,delay=0,duration=500}){
    if (bothInvisible){
            return {
                 delay:0,
                duration:0}
        }
    console.log(`migrateLeftExploit:${replaceExploit}`)
        if (replaceExploit.truth){
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${(viewPortScale/(viewNumber+1))*u}vw)) `
            }
        }
        else{
            return {}
        }
}
    function migrateOut(node,{replaceExploit,delay=0,duration=500}){
        if (bothInvisible){
            return {
                 delay:0,
                duration:0}
        }
        console.log(`migrateOut:${replaceExploit}`)
        if (replaceExploit.truth){
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${-100*u}vw)) `
        }
        }
        else{
            return {}
        }
       }
    function InvisibleOrDown(node,{replaceExploit,delay=0,duration=500}){
        if (bothInvisible){
            return {
                 delay:0,
                duration:0}
        }
        console.log(`invisibleOrDown:${replaceExploit}`)
        if (! replaceExploit.truth){
            return {
            delay,
            duration,
            css: (t,u)=> `transform: translateY(calc(${100*u}vh)) `
        }}
        else{
            return {
            css: ()=> `visibility: hidden;display: none;`
        }
        }
    }
    async function timer(time){
        return await new Promise(r => setTimeout(r, time));
    }
    async function handleKeydown(event){
        console.log(event.key)
        if (keyView==false){
            return
        }
        if (event.key === "ArrowLeft" || event.key === "ArrowRight"){
            let singleTrialData={trial:trial.toString(),previousExploit:exploitMu,keyPressTime:Date.now(),trialStartTime:trialStartTime}
            bothInvisible=false
            if(event.key == "ArrowLeft"){
                keyView=false
                let newDist=sample_normal_to_twenty()
                singleTrialData["newExploit"] = newDist
                singleTrialData["choice"] = "exploit"
                singleTrialDate["exploreSeen"]=undefined
                exploitSelect=true
                await timer(500)
                exploitSelect=false
                clearBoard=true
                singleTrialData["exploitBoardClear"]=Date.now()
                await timer(1000)
                exploitMu=newDist
                clearBoard=false
                keyView=true
                singleTrialData["newExploitBoard"]=Date.now()
                trialStartTime = Date.now()
                currentUnderstanding=newDist
                trial+=1
                console.log("done")
        }
        if(event.key=="ArrowRight"){
            viewExplore=true
            let newDist=random_int(20)
            singleTrialData["choice"] = "explore"
            singleTrialDate["exploreSeen"]=newDist
            if (newDist > exploitMu){
                singleTrialData["newExploit"] = newDist
                console.log("greater than")
                keyView=false
                exploreMu=newDist
                exploreSelect=true
                singleTrialData["newExploreVisible"] = Date.now()
                await timer(500)
                exploreSelect=false
                singleTrialData["newExploreDeslected"] = Date.now()
                await timer(500)
                exploitMu=newDist
                viewExplore=false
                replaceExploit.truth=true
                counter+=1
                singleTrialData["newExploreMove"] = Date.now()
                await timer(500)
                keyView = true
                singleTrialData["exploreFinishedMoving"] = Date.now()
                trialStartTime=Date.now()
                currentUnderstanding=newDist
                trial+=1
            }
        else{
            console.log("less than")
            keyView=false
            singleTrialData["newExploit"] = null
            exploreMu=newDist
            exploreSelect=true
            singleTrialData["newExploreVisible"] = Date.now()
            await timer(500)
            exploreSelect=false
            singleTrialData["newExploreDeselected"] = Date.now()
            await timer(500)
            singleTrialData["newExploreMove"] = Date.now()
            viewExplore=false
            replaceExploit.truth=false
            counter+=1
            await timer(500)
            keyView = true
            singleTrialData["exploreFinishedMoving"] = Date.now()
            trialStartTime=Date.now()  
            currentUnderstanding=newDist
            trial+=1
            }
        }
        bothInvisible=true
        export_data(singleTrialData)
        if (trial === numTrials+1){
            toDebrief()
        }
        }
      
    }
    function box_mueller() {
    // all credit to stack exhange
        var u = 0, v = 0;
        while(u === 0) u = Math.random(); 
        while(v === 0) v = Math.random();
        return Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );
}
    function sample_normal(mu,sd){
        return sd*box_mueller()+mu
    }
    function sample_normal_to_twenty(){
        let newNorm=Math.floor(sample_normal(exploitMu,trialSd))
        newNorm=Math.min(newNorm,20)
        newNorm=Math.max(newNorm,0)
        return newNorm
    }
    function random_int(){
        return Math.floor(20*Math.random())
    }
    function export_data(data){
        let iterate_keys=[
            "trial",
            "previousExploit",
            "keyPressTime",
            "trialStartTime",
            "choice",
            "newExploit",
            "exploreSeen",
            "exploitBoardClear",
            "newExploitBoard",
            "newExploreVisible",
            "newExploreDeselected",
            "newExploreMove",
            "exploreFinishedMoving",
        ];
        let trialString=""
        for (const key of iterate_keys){
            trialString+=`${data[key]},`
        }
        gameString+=trialString.substring(0,trialString.length -2) +"\n"
        console.log(gameString)
    }
    //in:migrateLeft out:migrateOut
</script>
<style>
.greyBox {
    position: absolute;
    background-color: rgb(207, 202, 202);
}
</style>
{#key counter}
<h1 style="position: absolute; top:0vh; left:calc(50vw + -400px); width:800px; height:50px; text-align:center; border:solid black 2px">Current Classroom Understanding: {Math.round(currentUnderstanding/20*100)}%</h1>
<h1 style="position: absolute; top:0vh; left:calc(100vw + -210px); width:200px; height:50px; text-align:center; border:solid black 2px">{trial} of {numTrials}</h1>
    {#each range as i}
        {#if counter<numTrials}
                <div style=" position: absolute; top:25vh; left:calc({(i+1)*Math.round(viewPortScale/(viewNumber+1))}vw - 215px);">
                {#if i==0}
                <h1 style="top:-100px; width: 430px; height:5px; position: absolute; text-align:center">Current Teaching Move</h1>
                <div style="width:{450}px;height:{450}px;border:solid blue 5px; opacity: {(!exploitSelect)?"0":"1"};top: 37px; left: -12px; position: absolute;"></div>
                    <div class="greyBox" id={`box1: ${counter}`} style="width:{blockSize}; top: 50px; height:{blockSize}; border: solid black 3px; margin: 0px" in:migrateLeftExploit={{replaceExploit:replaceExploit}} out:migrateOut={{ replaceExploit: replaceExploit}}>
                        <div style="top: 50px; position: absolute">
                            {#if !clearBoard}
                                <RedGreen numberGreen={exploitMu}/>
                            {:else}
                                <RedGreen numberGreen={0} clearBoard={true}/>
                            {/if}
                        </div>
                    </div>
                {#if keyView}
                    <div style="top:500px; left: 0px; width: 430px; display:flex; justify-content:center; position: absolute;">
                        <h2 style=" width: 200px; border: solid black 3px; text-align: center">Left Arrow</h2>
                    </div>
                {/if}
                {:else}
                <h1 style="top: -100px; width: 430px; height:5px; position: absolute; text-align:center">New Teaching Move</h1>
                <div class="greyBox" id={`box2: ${counter}`} style="width:{blockSize}; top:50px; height:{blockSize}; border: solid black 3px; margin: 0px" in:migrateLeftExplore={{replaceExploit:replaceExploit}} out:InvisibleOrDown={{replaceExploit:replaceExploit}}>
                    <div style="top: 50px; position: absolute">
                        {#if viewExplore}
                            <RedGreen numberGreen={exploreMu}/>
                        {:else}
                            <div style="width: 430px; text-align: center; font-size: 200px">?</div>
                        {/if}
                    </div>
                 </div>
                 <div style="width:{450}px;height:{450}px;border:solid blue 5px; opacity: {(!exploreSelect)?"0":"1"};top: 37px; left: -12px; position: absolute;"></div>
            {#if keyView}
                 <div style="top:500px; left: 0px; width: 430px; display:flex; justify-content:center; position: absolute;">
                    <h2 style=" width: 200px; border: solid black 3px; text-align: center">Right Arrow</h2>
                </div>
            {/if}
            {/if}
            </div>
        {/if}
    {/each}
{/key}
