<script>
    import RedGreen from "../RedGreen.svelte"
    export let breakNav
    export let counter=0
    export const viewNumber=2
    let viewPortScale=100
    let blockSize="430px"
    let range=[...Array(viewNumber).keys()]
    export let delayGoodExplore=false
    export let delayBadExplore=false
    export let delayExploit = false
    export let viewExplore=false
    export let exploitMu=12
    export let exploreMu=5
    export let exploitMu2=14
    export let exploreSelect =false
    export let exploitSelect =false
    export let replaceExploit={truth:true}
    export let clearBoard=false
    export let bothInvisible={truth:true}
    export let keyDisplay = false
    export let noReplaceExplore=false
    export let pointCounter=false
    export let points=14
    export let delayTime=1000
    let invisibleExplore=false
    let keyView=keyDisplay
    if (delayGoodExplore){
        delayedGoodExplore()
    }
    if (delayBadExplore){
        delayedBadExplore()
    }
    if (delayExploit){
        delayedExploit()
    }
function migrateLeftExplore(node,{delay=0,duration=500}){
        if (bothInvisible.truth){
            return {
                delay:0,
                duration:0
            }
        }
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${100*u}vw)) `
            }
}
function migrateLeftExploit(node,{replaceExploit,delay=0,duration=500}){
    if (bothInvisible.truth){
            return {
                 delay:0,
                duration:0}
        }
        if (replaceExploit.truth){
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${(viewPortScale/(viewNumber+1))*u}vw)) `
            }
        }
        else{
            return {}
        }
}
    function migrateOut(node,{replaceExploit,delay=0,duration=500}){
        if (bothInvisible.truth){
            return { 
                delay:0,
                duration:0}
        }
        if (replaceExploit.truth){
            return {
                delay,
                duration,
                css: (t,u)=> `transform: translateX(calc(${-100*u}vw)) `
        }
        }
        else{
            return {}
        }
       }
    function InvisibleOrDown(node,{replaceExploit,delay=0,duration=500}){
        if (bothInvisible.truth){
            return {
                delay:0,
                duration:0
            }
        }
        if (! replaceExploit.truth){
            return {
            delay,
            duration,
            css: (t,u)=> `transform: translateY(calc(${100*u}vh)) `
        }}
        else{
            return {
            css: ()=> `visibility: hidden;display: none;`
        }
        }
    }
    async function timer(time){
        return await new Promise(r => setTimeout(r, time));
    }

    async function delayedGoodExplore(){
        if (keyDisplay) keyView=false
        breakNav(true)
        await timer(delayTime)
        bothInvisible={truth:false}
        viewExplore=true
        exploreSelect=true
        await timer(500)
        exploreSelect=false
        await timer(1000)
        exploitMu=exploreMu
        viewExplore=false
        replaceExploit.truth=true
        counter+=1
        if (noReplaceExplore) invisibleExplore=true
        await timer(500)
        breakNav(false)
        if (keyDisplay) keyView=true
        bothInvisible={truth:true}
    }
    async function delayedBadExplore(){
        if (keyDisplay) keyView=false
        breakNav(true)
        await timer(delayTime)
        bothInvisible={truth:false}
        viewExplore=true
        exploreSelect=true
        await timer(500)
        exploreSelect=false
        await timer(1000)
        viewExplore=false
        replaceExploit.truth=false
        counter+=1
        if (noReplaceExplore) invisibleExplore=true
        await timer(500)
        breakNav(false)
        if (keyDisplay) keyView=true
        bothInvisible={truth:true}
    }
    async function delayedExploit(){
        if (keyDisplay) keyView=false
        breakNav(true)
        await timer(delayTime)
        exploitSelect=true
        bothInvisible={truth:false}
        await timer(500)
        clearBoard=true
        exploitSelect=false
        await timer(1000)
        clearBoard=false
        exploitMu=exploitMu2
        counter+=1
        breakNav(false)
        if (keyDisplay) keyView=true
        bothInvisible={truth:true}
    }
    </script>
<style>
.greyBox {
    position: absolute;
    background-color: rgb(207, 202, 202);
}
</style>
{#key counter}
    {#if pointCounter}
        <h1 style="position: absolute; top:0vh; left:calc(50vw + -400px); width:800px; height:50px; text-align:center; border:solid black 2px">Current Classroom Understanding: {Math.round(points/20*100)}%</h1>
    {/if}
    {#each range as i}
        <div style=" position: absolute; top:25vh; left:calc({(i+1)*Math.round(viewPortScale/(viewNumber+1))}vw - 215px);">
        {#if i==0}
        <h1 style="top:-50px; width: 430px; height:5px; position: absolute; text-align:center">Current Teaching Move</h1>
        <div style="width:{450}px;height:{450}px;border:solid blue 5px; opacity: {(!exploitSelect)?"0":"1"};top: 37px; left: -12px; position: absolute;"></div>
            <div class="greyBox" id={"trial1"} style="width:{blockSize}; top: 50px; height:{blockSize}; border: solid black 3px; margin: 0px" in:migrateLeftExploit={{replaceExploit:replaceExploit}} out:migrateOut={{ replaceExploit: replaceExploit}}>
                <div style="top: 50px; position: absolute">
                    {#if !clearBoard}
                        <RedGreen numberGreen={exploitMu}/>
                    {:else}
                        <RedGreen numberGreen={0} clearBoard={true}/>
                    {/if}
                </div>
            </div>
        {:else}
        <h1 style="top: -50px; width: 430px; height:5px; position: absolute; text-align:center">New Teaching Move</h1>
        <div class="greyBox" id="trial2" style="width:{blockSize}; top:50px; height:{blockSize}; border: solid black 3px; margin: 0px; display:{(invisibleExplore)?"none":""}" in:migrateLeftExplore={{replaceExploit:replaceExploit}} out:InvisibleOrDown={{replaceExploit:replaceExploit}}>
            <div style="top: 50px; position: absolute">
                {#if viewExplore}
                    <RedGreen numberGreen={exploreMu}/>
                {:else}
                    <div style="width: 430px; text-align: center; font-size: 200px">?</div>
                {/if}
            </div>
            </div>
            <div style="width:{450}px;height:{450}px;border:solid blue 5px; opacity: {(!exploreSelect)?"0":"1"};top: 37px; left: -12px; position: absolute;"></div>
    {/if}
    {#if keyView}
        <div style="top:500px; left: 0px; width: 430px; display:flex; justify-content:center; position: absolute;">
            <h2 style=" width: 200px; border: solid black 3px; text-align: center">{(i===0)?"Left Arrow":"Right Arrow"}</h2>
        </div>
    {/if}
    </div>

    {/each}
{/key}