<script>
    export let numberGreen=0
    export let clearBoard=false
    if (!numberRed){
        numberRed=20-numberGreen
    }
    $: numberRed=(!clearBoard)?(20-numberGreen):0
    $: rangeGreen=[...Array(numberGreen).keys()]
    $: rangeRed=[...Array(numberRed).keys()]
    let stackSize=5
    let ballSize=50
   
</script>
<style>
.redDot{
    border-radius: 50%;
    background-color: red
}
.greenDot{
    border-radius: 50%;
    background-color: green
}
</style>
    <div>
    <div style="left:10px; width: 200px; height: 0px; position: absolute; text-align:center">
        <h style="width: 200px; color: black;">Green Light Students</h>
        <div style="left: -5px; width:205px;height:300px;border:solid black 1px; position:absolute"></div>
        {#each rangeGreen as i}
            <div class= "greenDot" style="left:{Math.floor(i/stackSize)*ballSize}px; top: {(stackSize-i%stackSize)*ballSize}px; width: {ballSize}px; height: {ballSize}px; position: absolute;"></div>
        {/each}
        </div>
    </div>
    <div>
    <div style="left:220px; width: 200px; height: 100px; position: absolute; text-align:center">
        <h style="width: 120px; color: black; text-align: center">Red Light Students</h>
        <div style="left: -5px; width:205px;height:300px;border:solid black 1px; position:absolute"></div>
        {#each rangeRed as j}
            <div class= "redDot" style="left:{Math.floor(j/stackSize)*ballSize}px; top: {(stackSize-j%stackSize)*ballSize}px; width: {ballSize}px; height: {ballSize}px; position: absolute"></div>
        {/each}
    </div>
</div>
